.cartitems{
    margin: 100px 170px;
}
.cartitems hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}
.cartitemformatmain{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-family: 600;
}
.cartitemsformat{
    font-size: 17px;
    font-weight: 500;
}
.carticonproducticon{
    height: 62px;

}
.carticonremove{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}

.cartItemQuantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #ffffff;
}
.cartitemdown{
    display: flex;
    margin: 100px 0px;

}
.cartitemstotal{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}
.cartitemtotalitem{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitemstotal button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: white;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitempromocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitempromocode p{
    color: #555;
}
.cartitempromobox{
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #eaeaea;

}
.cartitempromobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.cartitempromobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    cursor: pointer;
}