.offer{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 60px;
    background: linear-gradient(180deg, #fde1ff, #e1ffea);
}

.offer_left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.offer_left h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;
}

.offer_left p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}

.offer_left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background-color: #ff1414;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offer_right{
    display: flex;
    /* align-items: center; */
    /* justify-content: flex-end; */
    padding-top: 25px;
    /* background: #ff1414; */
}