.newletter{
    width: 60%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff, #e1ffea);
    gap: 30px;
}

.newletter h1{
    color: #454545;
    font-size: 50px;
    font-weight: 600;
}

.newletter p{
    color: #454545;
    font-size: 20px;
    
}

.newletter div{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border:  1px solid #e3e3e3;
}

.newletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}

.newletter div button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}